.dynamic-text {
  position: relative;
  display: flex;
  
}
.dynamic-text .simply {
  
}
.dynamic-text #text-target {
  display: flexbox;
  width: 300px;
  text-align: left;
  animation: color 30s alternate infinite;
  perspective: 200px;
  transform: translateX(26px);
}
.dynamic-text .word {
  position: absolute;
  
}
@keyframes color {
  0% {
    color: black;
 }
  50% {
    color: rgb(255, 196, 0);
 }
  100% {
    color: red;
 }
}
.dynamic-text .letter {
  transform: translateZ(200px);
}
@keyframes anim {
  25% {
    transform: translateZ(0px);
    opacity: 1;
 }
  32% {
    opacity: 0;
    transform: translateZ(-280px) translateX(200px);
 }
}
@media (max-width: 650px) and (min-width: 521px) {
  .dynamic-text {
      width: 49% !important;
  }}